/* popup */
#stationDetail.content__body {
  display: flex;
  justify-content: space-between;
}

#stationDetail.content__body > div:first-of-type {
  width: 40%;
  border-right: 1px solid rgba(34, 36, 38, 0.15);
}

#stationDetail.content__body > div:last-of-type {
  width: 60%;
  min-height: 314px;
}

#stationDetail.content__body > .item__list {
  max-height: 400px;
  overflow-y: auto;
}
#stationDetail.content__body > .item__list > ul {
  box-sizing: border-box;
  padding: 16px;
  cursor: pointer;
  border-bottom: 1px solid rgba(34, 36, 38, 0.15);
}

#stationDetail.content__body > .item__list > ul.selected {
  background: var(--main-color);
  color: white;
}
#stationDetail.content__body > .item__list > ul.selected:hover {
  background: var(--main-color);
}
#stationDetail.content__body > .item__list > ul:hover {
  background: rgba(207, 207, 207, 0.2);
}

#stationDetail.content__body > .item__list > ul > li {
  margin-bottom: 14px;
}

#stationDetail.content__body > .item__list > ul li:last-of-type {
  margin-bottom: 0px;
}

#stationDetail.content__body > .item__list > ul li dl {
  display: flex;
  align-items: center;
}

/* // popup */
.content.station__add__update .station__input__wrap .section__inner:after {
  content: '';
  display: block;
  clear: both;
}

.content.station__add__update .station__input__wrap .section__inner > .input__wrap {
  width: 345px;
  position: sticky;
  top: 74px;
  float: left;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap {
  width: calc(100% - 365px);
  position: sticky;
  top: 74px;
  float: right;
}

.content.station__add__update .station__input__wrap .section__inner > .input__wrap > ul > li dt,
.content.station__add__update .station__input__wrap .section__inner > .map__wrap > ul > li dt {
  margin-bottom: 12px;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap > dl {
  margin-bottom: 16px;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap > dl dt {
  margin-bottom: 12px;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap > dl dd {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap > dl dd button {
  margin-left: 16px;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .input__wrap
  > ul
  > li
  dd
  select {
  width: 100%;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .input__wrap
  > ul
  > li
  dd.setting {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .input__wrap
  > ul
  > li
  dd.setting
  select {
  margin-right: 14px;
}

.content.station__add__update .station__input__wrap .section__inner > .input__wrap > ul > li {
  margin-bottom: 16px;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .input__wrap
  > ul
  > li:last-of-type {
  margin-bottom: 0px;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .input__wrap
  > ul
  > li
  textarea {
  min-height: 96px;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap #map {
  height: 600px;
  margin-bottom: 16px;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap #map > div {
  height: 100%;
}

.content.station__add__update .station__input__wrap .section__inner > .map__wrap > ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .map__wrap
  > ul
  li:first-of-type {
  width: 48%;
}

.content.station__add__update
  .station__input__wrap
  .section__inner
  > .map__wrap
  > ul
  li:nth-of-type(2),
.content.station__add__update
  .station__input__wrap
  .section__inner
  > .map__wrap
  > ul
  li:nth-of-type(3) {
  width: 24%;
}

@media (max-width: 750px) {
  .content.station__add__update .station__input__wrap .section__inner {
    flex-direction: column-reverse;
    display: flex;
  }

  .content.station__add__update .station__input__wrap .section__inner > div {
    width: 100% !important;
    position: unset !important;
    float: none !important;
  }

  .content.station__add__update .station__input__wrap .section__inner > .map__wrap #map {
    height: 400px;
  }

  .content.station__add__update .station__input__wrap .section__inner > .map__wrap > ul {
    margin-bottom: 16px;
  }
}

@media (max-width: 550px) {
  .content.station__add__update .station__input__wrap .section__inner > .map__wrap > ul {
    flex-wrap: wrap;
  }

  .content.station__add__update
    .station__input__wrap
    .section__inner
    > .map__wrap
    > ul
    li:first-of-type {
    width: 100%;
    margin-bottom: 16px;
  }

  .content.station__add__update
    .station__input__wrap
    .section__inner
    > .map__wrap
    > ul
    li:nth-of-type(2),
  .content.station__add__update
    .station__input__wrap
    .section__inner
    > .map__wrap
    > ul
    li:nth-of-type(3) {
    width: 48%;
  }
}
